import gsap from 'gsap'
import axios from 'axios'

import BreadCrumb from '@/components/Blocks/BreadCrumb'
import DropDown from '@/components/DropDown'
import ArticleCard from '@/components/Card/ArticleCard'

import functions from '@/compositions/functions'
const { loadImage } = functions()

export default {
    components: {
        BreadCrumb,
        DropDown,
        ArticleCard,
    },
    async asyncData ({ $config, $axios, route, error }) {
        try {
            const query = route.query
            let res
            let data
            let filter = {
                program: '',
                facet: '',
            }
            if (query['cat-program'] || query['cat-facet']) {
                res = await $axios.$get(`${$config.APP_BACKEND_API}/wp-json/api/courses/filter?cat-program=${query['cat-program']}&cat-facet=${query['cat-facet']}`)
                data = res.data
                filter = {
                    program: query['cat-program'] || '',
                    facet: query['cat-facet'] || '',
                }
            } else {
                res = await $axios.$get(`${$config.APP_BACKEND_API}/wp-json/api/courses`)
                data = res.data
            }

            const seo = data.seo
            const totalPosts = data.posts
            const currentPosts = totalPosts.slice(0, 12)
            const totalPage = Math.ceil(totalPosts.length / 12)

            const facet = []
            for (const prop in data.categories['cat-facet']) {
                facet.push({
                    name: data.categories['cat-facet'][prop].name,
                    slug: data.categories['cat-facet'][prop].slug,
                })
            }

            const program = []
            for (const prop in data.categories['cat-program']) {
                program.push({
                    name: data.categories['cat-program'][prop].name,
                    slug: data.categories['cat-program'][prop].slug,
                })
            }

            return {
                data,
                seo,
                currentPosts,
                totalPosts,
                totalPage,
                facet,
                program,
                filter,
            }
        } catch (e) {
            console.log(e)
            error({ statusCode: 404, message: 'Post not found' })
            return {
                data: {},
                seo: {},
                currentPosts: '',
                totalPosts: [],
                totalPage: 0,
                facet: [],
                program: [],
                filter: {
                    program: '',
                    facet: '',
                },
            }
        }
    },
    data () {
        return {
            currentPosts: [],
            totalPosts: [],
            currentPage: 1,
            totalPage: 0,
            filter: {
                program: '',
                facet: '',
            },
        }
    },
    head () {
        return {
            title: this.seo?.title,
            meta: [
                { hid: 'description', name: 'description', content: this.seo?.desc },
                { hid: 'og:title', property: 'og:title', content: this.seo?.title },
                { hid: 'og:description', property: 'og:description', content: this.seo?.desc },
                { hid: 'og:url', property: 'og:url', content: `${process.env.APP_URL}` },
                { hid: 'og:site_name', property: 'og:site_name', content: this.seo?.title },
                { hid: 'og:image', property: 'og:image', content: this.seo?.thumb ? this.seo?.thumb : `${process.env.APP_URL}/og_img.jpg` },
                { hid: 'og:image:alt', property: 'og:image:alt', content: this.seo?.desc },
                { hid: 'twitter:description', name: 'twitter:description', content: this.seo?.desc },
                { hid: 'twitter:title', name: 'twitter:title', content: this.seo?.title },
                { hid: 'twitter:image', name: 'twitter:image', content: this.seo?.thumb ? this.seo?.thumb : `${process.env.APP_URL}/og_img.jpg` },
            ],
        }
    },
    mounted () {
        this.$store.dispatch('ADD_LOADING_STACK', loadImage())
        this.$store.dispatch('WAIT_LOADING')

        this.gsapInit()
        this.resizeIntro()
        window.addEventListener('resize', this.resizeIntro)
    },
    beforeDestroy () {
        window.removeEventListener('resize', this.resizeIntro)
    },
    methods: {
        updatePage () {
            this.currentPage += 1
            const prevPostsLength = this.currentPosts.length
            const newPosts = this.totalPosts.slice((this.currentPage - 1) * 12, (this.currentPage - 1) * 12 + 12)
            this.currentPosts.push(...newPosts)
            this.$nextTick(function () {
                const els = gsap.utils.toArray('.courses__articles-item').slice(prevPostsLength)
                gsap.fromTo(els, { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 })
            })
        },
        async filterPage (val) {
            this.filter = { ...this.filter, ...val }

            this.$router.replace({ path: '/courses', query: { 'cat-program': this.filter.program, 'cat-facet': this.filter.facet } })

            const res = await axios.get(`${this.$config.APP_BACKEND_API}/wp-json/api/courses/filter?cat-program=${this.filter.program}&cat-facet=${this.filter.facet}`)
            const data = res.data.data

            this.totalPosts = data.posts
            this.currentPosts = data.posts.slice(0, 12)
            this.totalPage = Math.ceil(data.posts.length / 12)
            this.currentPage = 1

            this.$nextTick(function () {
                const els = gsap.utils.toArray('.courses__articles-item')
                gsap.fromTo(els, { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 })
            })
        },
        gsapInit () {
            gsap.fromTo('.breadcrumb', { y: -20, opacity: 0 }, { y: 0, opacity: 1 }).delay(1)
            gsap.fromTo('.courses__title', { x: 20, opacity: 0 }, { x: 0, opacity: 1 }).delay(1)
            gsap.fromTo('.courses__intro__image', { opacity: 0 }, { opacity: 1 }).delay(1)
            gsap.fromTo('.courses__intro__content', { opacity: 0 }, { opacity: 1 }).delay(1)
            gsap.fromTo('.courses__intro__content > div', { y: 20, opacity: 0 }, { y: 0, opacity: 1 }).delay(1.3)
            gsap.fromTo('.courses__filter__item', { x: 20, opacity: 0 }, { x: 0, opacity: 1, stagger: 0.1 }).delay(1.3)

            const tl = gsap.timeline()
            tl.fromTo('.courses__articles-item', { y: 30, opacity: 0 }, { y: 0, opacity: 1, stagger: 0.2 }).delay(1.6)
        },
        resizeIntro () {
            this.$refs.intro.style.height = null

            if (window.innerWidth >= 1200) {
                this.$refs.intro.style.height = this.$refs.introContent.clientHeight + 'px'
            }
        },
    },
}
